import React, { Component } from 'react'
import ClickNHold from 'react-click-n-hold'

class BoxButton extends Component {
  render () {
    const {label, longClickDelay} = this.props
    return (
      <ClickNHold
        time={longClickDelay}
        onEnd={this.handleClick}>
        <div className='Button'>
          {label}
        </div>
      </ClickNHold>
    )
  }

  handleClick = (e, enough) => {return enough ? this.props.onLongClick() : this.props.onClick()}

}

BoxButton.defaultProps = {
  longClickDelay: 1.7,
  onLongClick: () => {},
  onClick: () => {}
}
export default BoxButton
