import React, { Component } from 'react'

class Display extends Component {

  render () {
    const {isLive, amount, units} = this.props
    return (
      <div className={`Display ${isLive ? 'On' : 'Off'}`}>
        {isLive && <div className="Display-units">{units}</div>}
        {isLive && <div className="Display-amount">{amount}</div>}
      </div>
    )
  }
}

export default Display
