import React, { Component } from 'react'
import './App.scss'
import Display from './Display'
import BoxButton from './BoxButton'
import ControlButton from './ControlButton'
import close from './close.png'
import open from './open.png'

const UNIT = 'units'
const GR_IN_OZ = 0.035274

function getAmountByUnit (isUnit, amount, unitAmount, units) {
  let resolvedAmount = units === 'oz' ? amount * GR_IN_OZ : amount
  resolvedAmount = isUnit ? (resolvedAmount / unitAmount) : resolvedAmount
  return Math.round(100 * resolvedAmount) / 100
}

const padWithZeros = amount => {
  if ((amount * 100) % 100 === 0) {
    return `${amount}.00`
  }
  if ((amount * 10) % 100 === 0 && (amount * 10) % 10 !== 0) {
    return `${amount}0`
  }
  return amount
}

class App extends Component {

  constructor (props) {
    super(props)
    this.state = {
      hadChange: false,
      showAll: true,
      isLive: false,
      isCoverOpen: false,
      isUnit: false,
      totalAmount: 0,
      inputAmount: '',
      currentAmount: 0,
      unitAmount: 0.05,
      units: 'gr'
    }
  }

  timer = () => setTimeout(() => { // return the timeoutID
    console.log('sleep')
    this.setState({
      isLive: false
    })

  }, 15000)

  componentWillUpdate (nextProps, nextState) {
    if (this.timeoutID) {
      clearTimeout(this.timeoutID)
    }
    this.timeoutID = this.timer()
  }

  render () {
    const {isLive, isCoverOpen, totalAmount, units, isUnit, unitAmount, currentAmount, inputAmount} = this.state
    return (
      <div className="App">
        <div className="Control">
          <div>
            <div>{`Status: ${isLive ? 'On' : 'Off'}`}</div>
            <div>{`Cover: ${isCoverOpen ? 'Open' : 'Close'}`}</div>
            <div>{`Unit: ${unitAmount}`}</div>
            <div>{`Total Amount: ${totalAmount}`}</div>
            <div>{`Current Amount: ${currentAmount}`}</div>
          </div>
          <input
            onChange={event => isCoverOpen && this.setState({inputAmount: Number(event.target.value)})}
            type="number" className="Input" value={inputAmount}/>
          <div>
            <ControlButton
              className={'Button ControlButton'}
              onClick={() => this.onControlClickCover(
                totalAmount + inputAmount,
                currentAmount + inputAmount)}
              label='+'
            />
            <ControlButton
              className={'Button ControlButton'}
              onClick={() => this.onControlClickCover(
                totalAmount - inputAmount,
                currentAmount - inputAmount)}
              label='-'
            />
          </div>
          <ControlButton
            className={'Button'}
            onClick={this.onCoverButtonClick}
            label='Cover'
          />
        </div>
        <div className="Box">

          <Display
            isLive={isLive}
            amount={this.resolveDisplayAmount()}
            units={isUnit ? UNIT : units}/>
          <div className='Buttons'>
            <BoxButton
              onClick={this.onPowerButtonClick}
              onLongClick={this.onPowerButtonLongClick}
              label='Power'
            />
            <img height="200px" src={isCoverOpen ? open : close} alt=""/>
            <BoxButton
              onClick={this.onDisplayClick}
              onLongClick={this.onDisplayLongClick}
              label='Display'
            />
          </div>
        </div>
      </div>
    )
  }

  resolveDisplayAmount = () => {
    const {totalAmount, isUnit, unitAmount, currentAmount, showAll, hadChange, units} = this.state
    const iterationMode = hadChange && !showAll
    const amount = iterationMode ? currentAmount : totalAmount
    const shouldDisplayPlus = iterationMode && (currentAmount > 0)
    return (shouldDisplayPlus ? '+' : '') + padWithZeros(getAmountByUnit(isUnit, amount, unitAmount, units))
  }

  onControlClickCover = (totalAmount, currentAmount) => {
    const {hadChange, isCoverOpen} = this.state
    !hadChange && this.setState({
      currentAmount: 0,
      hadChange: true
    })
    isCoverOpen && this.setState({
      totalAmount,
      currentAmount,
      isLive: true,
    })
  }
  onDisplayClick = () => {
    const {isUnit} = this.state
    this.setState({isUnit: !isUnit})
  }
  onDisplayLongClick = () => {
    const {isUnit, units, totalAmount} = this.state
    if (isUnit) {
      this.setState({unitAmount: totalAmount})
    } else {
      this.setState({units: units === 'oz' ? 'gr' : 'oz'})
    }
  }
  onPowerButtonLongClick = () => {
    const {isCoverOpen} = this.state
    isCoverOpen && this.setState({currentAmount: 0})

  }
  onPowerButtonClick = () => {
    const {isLive, isCoverOpen, showAll} = this.state
    if (isLive) {
      if (isCoverOpen) {
        this.setState({showAll: !showAll})
      } else {
        this.turnOff()
      }
    } else {
      this.setState({isLive: true})
    }
  }
  onCoverButtonClick = () => {
    const {isLive, isCoverOpen} = this.state
    this.setState({
      isCoverOpen: !isCoverOpen,
      showAll: isCoverOpen
    })
    isCoverOpen && this.setState({currentAmount: 0, hadChange: false})
    !isLive && !isCoverOpen && this.setState({isLive: true})
  }
  // handleButtonPress = () => {
  //   this.buttonPressTimer = setTimeout(() => this.onPowerButtonLongClick(), 1500)
  // }
  // handleButtonRelease = () => clearTimeout(this.buttonPressTimer)
  turnOff = () => this.setState({isLive: false, hadChange: false, inputAmount: '', currentAmount: 0})
}

export default App
